import React, { Component } from "react";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Form, Button } from "react-bootstrap";
import FormTerms from "./Components/FormTerms";
import Industry from "./Components/Industry";
import { COMPANY_NAME } from "../../actions/api";
import PhoneInputField from "../Layout/Main/CommonComponents/PhoneInput";
import { getUserInfo, otherInfoSkip, otherInfoUpate } from '../../actions/loginActions';
import { formatTemplateText } from "../../utils";

class MoreInfo extends Component {
    state = { isValid: null, phone: "", country_code: null, countrySortname: "", industry: "", designation:"" }
    onIndustryChange = (value) => this.setState({ industry: value });
    onDesignationChange = (value) => this.setState({ designation: value });

    onClickToSkip = event => {
        const { history, getUserData } = this.props
        otherInfoSkip(history, () => getUserData(history, false))
    }

    onClickToFinish = event => {
        const { history, getUserData } = this.props
        const { phone, country_code, industry, isValid, designation } = this.state;
        const payload = {
            organization: "",
            industry: String(industry || ''),
            designation: String(designation || ''),
            country_code: phone ? String(country_code || '') : "",
            contact_no: (String(phone).includes(`+${country_code}`) ? String(phone).replace(`+${country_code}`, '') : phone)?.replace(/^0+/, '')
        }
        const phoneNumber = payload?.contact_no ?? "";
        if (phoneNumber || industry) {
            if(industry) otherInfoUpate(payload, history, () => getUserData(history, false));
            else if(phoneNumber && isValid) otherInfoUpate(payload, history, () => getUserData(history, false))
        } else this.onClickToSkip()
    }

    handleCountry = (country) => this.setState({ countrySortname: country });
    handlePhoneInput = (value, isValid, code) => {
        if (!value) this.setState({ isValid: true })
        else this.setState({ phone: value, isValid, country_code: code, countrySortname: code })
    }

    render() {
        const { phone, countrySortname, isValid } = this.state;
        const content = window?.APP_CONTENT?.init ?? {};

        return <>
            {window.location.host.includes('app') ? <Helmet script={[{
                type: 'text/javascript',
                innerHTML: `(function() { gtag('event', 'conversion', { 'send_to': 'AW-919733403/dpLYCN33g5UBEJuJyLYD' }) })()`
            }]}></Helmet> : null}
            
            <div className="w-100 mb-lg-0 mb-3">
            <div className="text-start">
                <h1 className=" mb-0 display-2 fw-bold">{formatTemplateText(content?.title, { COMPANY_NAME })}</h1>
            </div>
            <div className="mb-8">
                <Form.Group className="mb-4 my-5 py-5">
                    <Form.Label className="fw-bold mb-4" htmlFor="industry">{content?.industry_label ?? ""}</Form.Label>
                    <Industry type="industry" content={content} onIndustryChange={this.onIndustryChange} />
                </Form.Group>


                <Form.Group className="mb-4 my-5 py-5">
                    <Form.Label className="fw-bold mb-4" htmlFor="industry">{content?.designation_label ?? ""}</Form.Label>
                    <Industry type="Designation" content={content} onIndustryChange={this.onDesignationChange} />
                </Form.Group>


                <Form.Group className="mb-4 mb-xl-5 py-xl-4">
                    <Form.Label className="fw-bold d-block" htmlFor="comp_name">{content?.phone_label ?? ""}</Form.Label>
                    <PhoneInputField handlePhoneInput={this.handlePhoneInput} valid={isValid} number={phone} countrySortname={countrySortname} handleCountry={this.handleCountry} />
                </Form.Group>
            </div>
            <Form.Group className="mb-4 mb-xl-5">
                <Button size="md" onClick={countrySortname ? this.onClickToFinish : null} id="finish_info" className="me-4">{content?.submit_button ?? ""}</Button>
                {/* <Button variant="link text-gray-400 fw-medium active-primary" onClick={() => countrySortname ? this.onClickToSkip() : ''} id="skip_info" className="text-gray-100 text-decoration-underline">{content?.skip_button ?? ""}</Button> */}
            </Form.Group>
            <FormTerms />
        </div>
        </>
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getUserData: (history, callBack = 0) => dispatch(getUserInfo(history, callBack))
    }
}
export default connect(null, mapDispatchToProps)(MoreInfo);