import React, { useMemo, useState } from 'react';
import { Form } from 'react-bootstrap';


const Industry = ({ type, onIndustryChange, content }) => {
    const [selectedIndustry, setSelectedIndustry] = useState('');
    const [customIndustry, setCustomIndustry] = useState('');

    const handleIndustryChange = (industry) => {
        setSelectedIndustry(industry);
        if (industry !== 'Others') {
            onIndustryChange(industry);
            setCustomIndustry('');
        }
    };

    const handleCustomIndustryChange = (e) => {
        const value = e.target.value;
        setCustomIndustry(value);
        onIndustryChange(value);
    };


    const isIndustry = type === 'industry' 
    const industries = useMemo(() => isIndustry ? window?.APP_CONTENT?.profile?.industry_list ?? [] :  window?.APP_CONTENT?.profile?.designation_list ?? [], []);
    const placeholder = isIndustry ? content?.other_industries : content?.other_designation;

    return (
        <div className="industry-selector">
            <div className="d-flex flex-wrap gap-3">
                {industries?.map((industry, index) => (
                    <div
                        key={index}
                        className={`py-2 px-3 rounded cursor-pointer ${
                            selectedIndustry === industry.value ? 'bg-dark text-white' : 'bg-white text-muted'
                        }`}
                        onClick={() => handleIndustryChange(industry.value)}
                        style={{ cursor: 'pointer' }} >
                        {industry.label}
                    </div>
                ))}
            </div>
            {selectedIndustry === 'Others' && (
                <Form.Group>
                    <Form.Control
                        type="text"
                        placeholder={placeholder}
                        value={customIndustry}
                        onChange={handleCustomIndustryChange}
                        className="mt-2 py-2 px-3 rounded"
                        style={{maxWidth:'400px'}}
                    />
                </Form.Group>
            )}
        </div>
    );
};

export default Industry;
